<template>
  <div class="board-view">
    <div>
    <b-card-group deck>
        <b-card header-tag="header"
                footer-tag="footer">
            <h6 slot="header" class="mb-0"><b-badge variant="dark"></b-badge> {{this.form.writer}} 
            <b-badge variant="dark"></b-badge>{{this.form.createAt.substr(0,10)}} {{this.form.createAt.substr(11,2)}} {{this.form.createAt.substr(14,2)}} 
            <b-badge variant="dark"></b-badge> {{this.form.count}}
            <br><b-badge variant="dark"></b-badge> {{this.form.title}}</h6>
            <h6 slot="footer" v-for="(item, index) in form.comments"
                              v-bind:key="item._id">
                              <b-badge variant="dark">{{index}}</b-badge>
                              <b-badge variant="dark">{{item.name}}</b-badge> 
                              {{item.memo}} {{item.createAt.substr(0,10)}} {{item.createAt.substr(11,2)}} {{item.createAt.substr(14,2)}}
                              <b-badge pill href="#" v-on:click="deleteComment(item._id)" variant="danger" size="sm"></b-badge>
            </h6>
            <p class="card-text">{{this.form.contents}}</p>
        </b-card>
    </b-card-group>
    <div style="width:600px;">
      <h4><b-badge variant="dark"></b-badge></h4>
      <b-form @submit.prevent="addComment" v-on:keyup.enter="addComment">
      <b-form-textarea placeholder=" ." rows="2" max-rows="6" v-model="memo">
      </b-form-textarea><b-button type="submit" variant="primary" size="sm"> (Enter)</b-button>
      </b-form>
    </div>
    </div>
    <b-button-group size="sm">
    <b-button v-on:click="toBoard" variant="primary"></b-button>
    <b-button v-on:click="updateBoard" variant="primary"></b-button>
    <b-button v-on:click="deleteBoard" variant="danger"></b-button>
    </b-button-group>  
  </div>
</template>

<script>
export default {
  name: "boardView",
  data() {
   return {
     form: {
       _id: this.$route.params.id,
       title: "",
       contents: "",
       createAt: "",
       count: "",
       comments: [{
         name: "",
         memo: "",
       }]
     },
     memo: ""
   };
  },
  created(){
    this.$EventBus.$emit('removeTab', true);
      this.getBoardDetail();
  },
  methods: {
   getBoardDetail(){
    this.$http.get(`/api/board/view/${this.$route.params.id}`)
       .then(res => {
         this.form = res.data;
       });
   },
   deleteBoard(){
       this.$http.delete(`/api/board/posts/${this.$route.params.id}`)
       .then(res => {
         const status = res.status;
         if(status == 200){
           alert(' .');
           this.$router.push('/board')
         }else if(status == 203){
           alert('   .');
           this.$router.push('/board')
         }
       }).catch(err => {
         alert(err)
       })
   },
   toBoard(){
     this.$router.push('/board')
   },
   updateBoard(){
     this.$http.get(`/api/board/posts/${this.$route.params.id}`)
       .then((res) => {
         const status = res.status
         if(status == 200){
           this.$router.push({
             path: '/update/:id',
             name: 'board-update',
             params: {
               id: this.$route.params.id
             }
           })
         } else if (status == 203){
           alert("   .")
           this.$router.push('/board')
         }
       })
   },
   addComment(){
     let comment = {
       name: "",
       memo: "",
       boardId: Number
     }
     comment.name = this.$cookies.get('nickname')
     comment.memo = this.memo;
     comment.boardId = this.form._id;
     this.$http.post(`/api/board/comment`, comment)
     this.memo = ''
     this.getBoardDetail()
   },
   deleteComment(_id){
      this.$http.delete(`/api/board/comment`, {data: {boardId: this.$route.params.id, commentId: _id }})
      .then((res) => {
        const status = res.status
        if(status == 200){
          alert(' .');
          this.getBoardDetail()
        } else if (status == 203){
          alert("   .")
          this.$router.push('/board')
        }
      })
      
    }
  }
};
</script>



