<template>
  <div class="signup">
    <h1 align-h="center">This is Sign Up Page!</h1>
    <b-form @submit="postUser" @reset="onSubmit" v-if="show">

        <b-form-group id="ID-InputGroup" label="ID:" label-for="id-input">
            <b-form-input id="id-input"
                type="text"
                v-model="form.name"
                required
                placeholder="">
            </b-form-input>
        </b-form-group>

        <b-form-group id="password-InputGroup" label="Password:" label-for="pwd-input">
            <b-form-input id="pwd-input"
                type="password"
                v-model="form.password"
                required
                placeholder="">
            </b-form-input>
        </b-form-group>

      <b-form-group id="password-InputCheckGroup" label="Password check:" label-for="pwd-input-check">
        <b-form-input id="pwd-input-check"
                      type="password"
                      v-model="form.password_check"
                      required
                      placeholder="">
        </b-form-input>
      </b-form-group>

        <b-form-group id="nickname-InputGroup" label="Nickname:" label-for="nickname-input">
            <b-form-input id="nickname-input"
                type="text"
                v-model="form.nickname"
                required
                placeholder="">
            </b-form-input>
        </b-form-group>

        <b-form-group id="email-InputGroup" label="Email:" label-for="email-input">
            <b-form-input id="email-input"
                type="email"
                v-model="form.email"
                required
                placeholder=" ">
            </b-form-input>
        </b-form-group>

        <b-form-group id="major-InputGroup" label="Major:" label-for="majour-input">
        <b-form-select id="majour-input"
                      :options="majors"
                      required
                      v-model="form.major">
        </b-form-select>
        </b-form-group>
        <b-row align-h="center">
            <b-button type="submit" variant="primary" align-h="center"></b-button>
        </b-row>
    </b-form>
  </div>
</template>

<script>
export default {
  name: 'SignUp',
  data () {
    return {
      form: {
        email: '',
        name: '',
        nickname: '',
        password: '',
        password_check: '',
        major: null
      },
      majors: [
        { text: '', value: null },
        '', '', '', ''
      ],
      show: true
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      alert(JSON.stringify(this.form))
    },
    postUser (evt) {
      evt.preventDefault() // 
      if (this.form.password_check === this.form.password) {
        this.$http.post('/api/signup', { // 
          username: this.form.name,
          password: this.form.password,
          email: this.form.email,
          nickname: this.form.nickname,
          major: this.form.major
        })
          .then((res) => {
            const status = res.status
            console.log(status)
            // redirect logic
            if (status == '200') {
              alert('  .     .')
              this.$router.push('/')
            } else if (status == '202') {
              alert('  ID .')
            } else if (status == '204') {
              alert('    .')
            }
          }).catch((err) => {
            console.log(err)
          })
      } else {
        alert('   .')
      }
    }
  }
}
</script>



